___CSS_LOADER_EXPORT___.push([module.id, `.settings {
    white-space: nowrap;
    float: right;
    display: flow-root;
    position: absolute;
    z-index: 10;
    right: 0;
    top: 0;
    margin-right: 1em;
    margin-top: 1.2em;
    /* width: 10%; */
    max-width: 40em;
    min-width: 7em;
}

.fadeOut {
    z-index: 20;
    opacity: 0;
    transition: opacity 0.8s;
}

.fadeIn {
    z-index: 20;
    opacity: 1;
    transition: opacity 0.8s;
}

.settings_header {
    margin: 5px;
    padding: 5px;
}

.drawer_toggle {
    background-color: #333333;
    width: 30px;
    height: 30px;
    overflow: hidden;
    border-radius: 10px 0 0 10px;
    cursor: pointer;
}

.drawer {
    font-family: sans-serif;
    height: fit-content;
}

.drawer_content {
    max-width: 500px;
    padding-top: 0.1em;
    padding-bottom: 0.1em;
}

.clear_marked {
    display: flex;
    justify-content: end;
}
